.cv-wrapper {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.cv-container {
    font-family: Arial, sans-serif;
    color: #333;
    max-width: 800px;
    width: 100%;
    padding: 20px;
    border: 1px solid #ffffff;
    border-radius: 8px;
    background-color: #fff;
    box-sizing: border-box;
    /* Garantir que o padding é incluído na largura */
}

.cv-personal-info {
    display: flex;
    align-items: center;
    background-color: #f9f9f9;
    padding: 20px;
    margin-bottom: 20px;
    border-radius: 8px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.education-header {
    display: flex;
    align-items: center;
}

.education-info div {
    display: flex;
    align-items: center;
    margin-top: 5px;
}

.education-info div svg {
    margin-right: 5px;
}

.job-header {
    display: flex;
    align-items: center;
}

.job-header p {
    display: flex;
    align-items: center;
    margin-top: 5px;
}

.job-header p svg {
    margin-right: 5px;
}

.job-info {
    margin-top: 10px;
    margin-bottom: 10px;
}

.info-item {
    display: flex;
    align-items: center;
    margin-bottom: 5px;
}

.icon {
    margin-right: 5px;
}

.responsibilities-list {
    margin-top: 10px;
}

.cv-photo {
    text-align: center;
    margin-right: 60px;
}

.cv-photo img {
    width: 150px;
    height: 150px;
    border-radius: 50%;
    object-fit: cover;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.cv-photo h1 {
    margin-top: 10px;
    font-size: 1.8em;
    color: #333;
}

.cv-details {
    flex-grow: 1;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.cv-details p {
    margin: 5px 0;
    display: flex;
    align-items: center;
    font-size: 1em;
    color: #555;
    font-family: 'Verdana', sans-serif;
    /* Nova fonte para o texto */
}

.cv-details a {
    color: #555;
    text-decoration: none;
    /* Remover o sublinhado dos links */
}

.cv-details button {
    display: flex;
    align-items: center;
    background: none;
    border: none;
    font-size: 1em;
    font-family: 'Verdana', sans-serif;
    color: #555;
    cursor: default;
    padding: 0;
    margin: 5px 0;
    text-align: left;
    width: 100%;
}

.cv-details button:hover {
    background-color: #e0e0e0;
    border-radius: 5px;
}

.icon-text {
    margin-left: 10px;
    color: #333;
}

.cv-section {
    margin-bottom: 20px;
    page-break-inside: avoid;
    /* Evitar quebra de página dentro dessa seção */
}

.cv-section h2 {
    font-size: 1.5em;
    color: #000;
    border-bottom: 2px solid #ddd;
    padding-bottom: 5px;
}

.cv-job,
.cv-education {
    margin-bottom: 15px;
    page-break-inside: avoid;
    /* Evitar quebra de página dentro dessa seção */
}

.cv-job h3,
.cv-education h3 {
    margin: 0;
    font-size: 1.2em;
    color: #333;
}

.cv-job p,
.cv-education p {
    margin: 5px 0;
    font-size: 0.9em;
    color: #555;
}

.cv-job ul,
.cv-education ul {
    list-style-type: disc;
    margin: 5px 0 0 20px;
    padding: 0;
    color: #555;
}

.cv-job li,
.cv-education li {
    margin: 0;
    padding: 0;
}

.cv-buttons {
    display: flex;
    flex-direction: column;
    align-items: center;
    position: fixed;
    top: 50%;
    right: 100px;
    transform: translateY(-50%);
}

.language-buttons {
    display: flex;
    width: 100%;
    /* Garantir que ocupe 100% da largura do contêiner pai */
    margin-bottom: 10px;
}

.lang-button {
    flex: 1;
    /* Distribuir igualmente o espaço entre os botões */
    border: none;
    background: none;
    cursor: pointer;
}

.lang-button .flag {
    width: 32px;
    height: 24px;
    transition: opacity 0.3s ease;
}

/* Ajuste para a proporção ser mantida */
.lang-button .flag img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.lang-button.active .flag {
    opacity: 1;
}

.lang-button.inactive .flag {
    opacity: 0.3;
}

.cv-buttons button {
    width: 100%;
    /* Garantir que o botão de download ocupe 100% da largura do contêiner pai */
    height: 40px;
    padding: 10px 20px;
    font-size: 1em;
    cursor: pointer;
    border: none;
    border-radius: 5px;
    background-color: #f2f2f2;
    color: black;
    appearance: none;
    -webkit-appearance: none;
}

.cv-buttons button:hover {
    background-color: #dcdcdc;
}

.icon-text {
    margin-left: 10px;
}

.cv-digital-skills {
    display: flex;
    flex-wrap: wrap;
    page-break-inside: avoid;
    /* Evitar quebra de página dentro dessa seção */
}

.cv-skill-button {
    margin-right: 10px;
    margin-bottom: 10px;
    padding: 10px 20px;
    font-size: 1em;
    cursor: default;
    border: none;
    border-radius: 5px;
    background-color: #f2f2f2;
    color: black;
    appearance: none;
    -webkit-appearance: none;
}

.cv-skill-button:hover {
    background-color: #dcdcdc;
}

.cv-cert-button {
    margin-right: 10px;
    margin-bottom: 5px; /* Reduzindo a margem inferior para diminuir a altura */
    padding: 5px 20px; /* Reduzindo o preenchimento para diminuir a altura */
    font-size: 1em;
    cursor: pointer; /* Alterado de 'default' para 'pointer' para indicar que é clicável */
    border: none;
    border-radius: 5px;
    background-color: #f2f2f2;
    color: black;
    appearance: none;
    -webkit-appearance: none;
}

.cv-cert-button:hover {
    background-color: #dcdcdc;
}

.cv-lang-buttons {
    display: flex;
    flex-direction: column;
}

.cv-lang-button {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 10px;
}

.cv-lang-text {
    text-align: left;
}

.cv-lang-text p {
    margin: 0;
    font-size: 1em;
    line-height: 1.2;
    /* Adicionado para ajustar a distância entre a linguagem e o nível */
}

.cv-lang-level {
    font-size: 0.8em;
    color: gray;
    margin-top: 2px;
    /* Diminuído para ajustar a distância entre a linguagem e o nível */
}

.cv-skill-level {
    display: flex;
}

.cv-skill-ball {
    width: 10px;
    height: 10px;
    margin-left: 5px;
    border-radius: 50%;
    border: 1px solid black;
}

.cv-skill-ball.filled {
    background-color: black;
}

.cv-skill-ball.empty {
    background-color: white;
}

.certificates-and-awards {
    margin-top: 20px;
}

.certificates-and-awards h2 {
    font-size: 1.5em;
    color: #333;
    border-bottom: 2px solid #ddd;
    padding-bottom: 5px;
}

.certificates-and-awards ul {
    list-style: none;
    padding: 0;
}

.certificates-and-awards li {
    margin-bottom: 10px;
}

.certificates-and-awards li span {
    color: #555;
}

.certificates-and-awards a {
    color: #007bff; /* Blue color for links */
    text-decoration: none;
}

.certificates-and-awards a:hover {
    text-decoration: underline;
}

/* Media Queries para telas menores */
@media (max-width: 600px) {
    .cv-container {
        padding: 10px;
    }

    .cv-personal-info {
        flex-direction: column;
        align-items: center;
        text-align: center;
    }

    .cv-photo {
        margin-right: 0;
        margin-bottom: 10px;
    }

    .cv-details {
        text-align: center;
    }

    .cv-buttons {
        position: static;
        transform: none;
        margin-bottom: 20px;
        width: 100%;
        /* Ajuste para alinhar os botões corretamente */
    }

    .cv-buttons button {
        width: 75%;
        /* Diminui a largura dos botões em ecrã mobile */
        font-size: 0.9em;
    }

    .cv-lang-button {
        width: 100%;
        /* Definir largura de 100% para ocupar toda a largura do contêiner pai */
        margin-right: 0;
        /* Remover a margem direita para evitar espaçamento extra */
        margin-bottom: 10px;
    }

    .language-buttons {
        display: flex;
        width: 75%;
        /* Garantir que ocupe 100% da largura do contêiner pai */
        margin-bottom: 10px;
    }
}